













































import { Component, Vue } from 'vue-property-decorator';
@Component({
  components: {},
})
export default class ViewComponent extends Vue {

  /**method */
  async debugOpenAccount(){
    const data = await this.$request({
      api: 'lmService/personalRegister',
      params:{
        token:this.$store.state.token,
        lmRegister:{
          realName: '孟琬',
          idCardNo: '210203197503102721',
          bankCardNo: '6228480402564890018',
          mobile: '15222018200',
        },
        originReturnUrl:this.$store.state.originNodeServer,
      },
    })
    if(data.resCode === '0000'){
      alert(data.returnUrl)
      this.$request({
        formSubmitData:{
          url:data.shUrl,
          params:data.params,
        }
      })
    }
  }
  async debugPersonalRegister(){
    this.$request({
      url: 'http://localhost:5000/lmService/personalRegisterReturn',
      params: {
        code: '0',
        status: 'SUCCESS',
      }
    })  
  }
}
